.App {
  text-align: center;
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between; 
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  box-sizing: border-box;
}

.Page-Content {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
}

.quiz-buttons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 40px;
  margin-bottom: 20px;
  position: static;
}

.quiz-buttons Button {
  background-color: #000000;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: 0px 4px 6px rgb(255, 255, 255);
  transition: all 0.2s ease-in-out;
}

.quiz-buttons Button:hover {
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.4);
  transform: translateY(-2px);
}

.quiz-buttons Button:active {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
  transform: translateY(2px);
}
